const SERVER_SEARCH_BAR = get_element("#server-search-bar");
const INNER_ICON_BLANK = get_element("#p-form__search-bar-status--blank");
const INNER_ICON_FILLED = get_element("#p-form__search-bar-status--filled");


//  (hmtlonclick)
clear_server_search_bar = () => {
    SERVER_SEARCH_BAR.value = "";
    INNER_ICON_BLANK.classList.remove("u-dn");
    INNER_ICON_FILLED.classList += " u-dn";
}


// placeholder
server_search_bar = () => {
    SERVER_SEARCH_BAR.addEventListener('input', () => {
        if (SERVER_SEARCH_BAR.value.trim() !== ""
        && !INNER_ICON_BLANK.classList.contains("u-dn")) {
            INNER_ICON_BLANK.classList += (" u-dn");
            INNER_ICON_FILLED.classList.remove("u-dn");
        } else if (SERVER_SEARCH_BAR.value.trim() === ""
        && !INNER_ICON_FILLED.classList.contains("u-dn")) {
            INNER_ICON_BLANK.classList.remove("u-dn");
            INNER_ICON_FILLED.classList += " u-dn";
        }
    })
}
if (SERVER_SEARCH_BAR) server_search_bar();




